@use "../default/mixins";
@use "../variables" as *;

.vacancies-list {
  width:                 100%;
  display:               grid;
  grid-template-columns: repeat(3, 1fr);

  @include mixins.mobile {
    display: block;
  }

  &__item {
    display:          flex;
    flex-direction:   column;
    padding:          24px 32px 32px;
    border:           1px solid $gray-80;
    margin:           -1px 0 0 -1px;
    background-color: $white;
    transition:       all $transition;

    @include mixins.laptopXL {
      padding: 14px 20px 20px;
    }

    @include mixins.mobile {
      padding:   12px;
      max-width: 335px;
    }

    h3, p, b {
      transition: all $transition;
    }

    p.text {
      margin-bottom: 46px;
      padding-left:  27px;
      position:      relative;

      @include mixins.laptopXL {
        margin-bottom: 34px;
      }

      @include mixins.mobile {
        margin-bottom: 24px;
      }

      &::before {
        content:          '';
        display:          block;
        width:            9px;
        height:           9px;
        background-color: $red;
        position:         absolute;
        left:             3px;
        top:              50%;
        transform:        translateY(-50%);
      }
    }

    b {
      font-family:   $second-family;
      font-weight:   400;
      font-size:     32px;
      margin-bottom: 28px;

      @include mixins.laptopXL {
        font-size:     20px;
        margin-bottom: 10px;
      }

      @include mixins.mobile {
        font-size: 18px;
      }
    }

    @include mixins.hover {
      background-color: $gray-80;
      h3, p, b {
        color: $white;
      }
    }

  }

  &__wrapper {
    flex:          1 0 auto;
    margin-bottom: 16px;

    @include mixins.laptopXL {
      margin-bottom: 14px;
    }

    @include mixins.hover {
      h3 {
        color: $red;
      }
    }
  }

  &__link.btn {
    transition: all $transition;
    @include mixins.hover {
      background-color: $white;
      color:            $black;
    }
  }
}