@use "../default/mixins";
@use "../variables" as *;

.page-template-about {
  .info {
    .container {
      padding-top:    54px;
      padding-bottom: 100px;

      @include mixins.laptopXL {
        padding-top:    40px;
        padding-bottom: 60px;
      }

      @include mixins.mobile {
        padding-top:    20px;
        padding-bottom: 0;
      }

    }

    .go-home {
      margin-bottom: 20px;

      @include mixins.laptopXL {
        margin-bottom: 16px;
      }
    }

    .section-title {
      margin-bottom: 80px;

      @include mixins.laptopXL {
        margin-bottom: 52px;
      }
      @include mixins.mobile {
        margin-bottom: 24px;
      }
    }

    &__inner {
      display:         flex;
      justify-content: space-between;
      column-gap:      100px;

      @include mixins.laptop {
        column-gap: 180px;
      }

      @include mixins.tablet {
        column-gap: 80px;
      }

      @include mixins.mobile {
        flex-direction: column;
      }

    }

    &__image {
      max-width: 476px;
      min-width: 290px;

      @include mixins.laptop {
        max-width: 290px;
      }

      @include mixins.mobile {
        max-width:     100%;
        height:        211px;
        margin-bottom: 20px;
      }

      img {
        width:  100%;
        height: auto;

        @include mixins.mobile {
          height:     100%;
          object-fit: cover;
        }
      }
    }

    &__description {
      max-width: 900px;

      > p {
        font-size:     24px;
        line-height:   117%;
        margin-bottom: 40px;

        @include mixins.laptopXL {
          font-size:     16px;
          margin-bottom: 32px;
        }

        strong {
          font-weight: 600;
        }

        &:last-of-type {
          margin-bottom: 32px;

          @include mixins.mobile {
            margin-bottom: 18px;
          }

        }
      }
    }
  }

  .building {
    background-color: $gray;

    .container {
      display:               grid;
      grid-template-columns: minmax(0, 567px) minmax(0, 900px);
      justify-content:       space-between;
      column-gap:            70px;

      @include mixins.mobile {
        display: block;
      }
    }

    .section-title {
      grid-row: span 2;

      @include mixins.mobile {
        margin-bottom: 24px;
      }
    }

    .text {
      margin-bottom: 40px;

      @include mixins.mobile {
        margin-bottom: 20px;
      }
    }

    &__image {
      width: 100%;

      img {
        max-width: 100%;
      }
    }
  }

  .partner {
    .container {
      display:               grid;
      grid-template-columns: minmax(0, 600px) minmax(0, 900px);
      justify-content:       space-between;

      @include mixins.mobile {
        display: block;
      }
    }

    .section-title {
      @include mixins.mobile {
        margin-bottom: 24px;
      }
    }
  }

  .team {
    background-color: $gray;

    .container {
      display:               grid;
      grid-template-columns: minmax(0, 600px) minmax(0, 900px);
      row-gap:               116px;
      justify-content:       space-between;

      @include mixins.mobile {
        display: block;
      }
    }

    .section-title {
      @include mixins.mobile {
        margin-bottom: 24px;
      }
    }

    .text {
      @include mixins.mobile {
        margin-bottom: 20px;
      }
    }

    &__list {
      grid-column:           span 2;
      display:               grid;
      grid-template-columns: repeat(3, 1fr);

      @include mixins.laptop {
        grid-template-columns: repeat(2, 1fr);
      }

      @include mixins.mobile {
        display: block;
      }
    }

    &__item {
      display:               grid;
      grid-template-columns: 233px 1fr;
      grid-template-rows: max-content max-content;
      column-gap:            32px;
      padding:               32px;
      border:                1px solid $gray-80;
      margin:                -1px 0 0 -1px;
      background-color:      $gray;
      position:              relative;

      @include mixins.laptopXL {
        grid-template-columns: 140px 1fr;
        padding:               20px;
        column-gap:            20px;
      }

      @include mixins.mobile {
        max-width: 350px;
      }


      &::before {
        content:        '';
        display:        block;
        width:          calc(100% + 2px);
        height:         calc(100% + 2px);
        position:       absolute;
        pointer-events: none;
        border:         1px solid transparent;
        top:            0;
        left:           0;
        margin:         -1px 0 0 -1px;
        z-index:        1;
      }

      @include mixins.hover {
        border-color: transparent;

        &:before {
          border-color: $red;
        }
        .team__image {
          filter: grayscale(0);
        }

      }

      h3 {
        margin-bottom: 24px;

        @include mixins.laptopXL {
          margin-bottom: 20px;
        }
      }
    }

    &__image {
      grid-row: 1/4;
      height:   330px;
      filter:   grayscale(100);

      @include mixins.laptopXL {
        height: 198px;
      }

      @include mixins.mobile {
        filter: grayscale(0);
      }

      img {
        width: 100%;
      }
    }

  }

}