@use "mixins" as *;
@use "../variables" as *;

a {
  text-decoration: unset;
  transition:      $transition color;
  color:           inherit;

  &[href^=tel] {
    white-space: nowrap;
  }
}

p {
  font-family: $font-family;
  font-weight: 400;
  font-size:   24px;
  line-height: 117%;

  @include laptopXL {
    font-size: 16px;
  }

  @include mobile {
    font-size: 14px;
  }
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 400;
  font-family: $second-family;
}

h1 {
  font-size:      48px;
  line-height:    120%;
  text-transform: uppercase;

  @include mobile {
    font-size: 32px;
  }
}

h2 {
  font-size:      54px;
  line-height:    100%;
  text-transform: uppercase;

  @include mobile {
    font-size: 28px;
  }
}

h3 {
  font-size:   40px;
  line-height: 105%;

  @include laptopXL {
    font-size:   24px;
    line-height: 108%;
  }

  @include mobile {
    font-size:   20px;
    line-height: 110%;
  }
}

h4 {
  font-size:   18px;
  line-height: 140%;

  @include mobile {
    font-size: 18px;
  }
}

h5 {
  font-size:   16px;
  line-height: 140%;

  @include mobile {
    font-size: 16px;
  }
}

li {
  font-weight: 400;
  font-size:   24px;
  line-height: 117%;

  @include laptopXL {
    font-size: 16px;
  }
  @include mobile {
    font-size: 14px;
  }
}

.wp-block-heading {
  margin-top:    80px;
  margin-bottom: 40px;

  @include laptopXL {
    margin-top:    40px;
    margin-bottom: 20px;
  }
  @include mobile {
    margin-top: 40px;
  }

}

ol.wp-block-list {
  font-weight:   600;
  font-size:     24px;
  line-height:   117%;
  margin-bottom: 40px;
  padding-left:  28px;

  @include laptopXL {
    font-size: 16px;
  }

  @include mobile {
    padding-left: 18px;
    font-size:    14px;
  }

  > li {
    &:not(:last-child) {
      margin-bottom: 24px;
    }

    &::marker {
      font-weight: 600;

      @include laptopXL {
        font-size: 16px;
      }
    }

    strong {
      display:       block;
      font-weight:   600;
      margin-bottom: 24px;

      @include laptopXL {
        font-size:     16px;
        margin-bottom: 18px;
      }

      @include mobile {
        line-height: 100%;
      }

    }

    ul {
      padding-left: 32px;

      @include laptopXL {
        padding-left: 12px;
      }

      @include mobile {
        padding-left: 22px;
      }

      > li {

      }
    }
  }
}

ul.wp-block-list {
  li {
    position:     relative;
    padding-left: 27px;

    @include laptopXL {
      font-size: 16px;
    }

    @include mobile {
      font-size: 14px;
    }


    &:not(:last-child) {
      margin-bottom: 8px;
    }

    &::before {
      content:          "";
      display:          block;
      width:            9px;
      height:           9px;
      position:         absolute;
      background-color: $red;
      top:              8px;
      left:             2px;

      @include mobile {
        top: 2px;
      }
    }
  }
}

.single {
  h3 {
    margin-bottom: 40px;
  }

  p {
    font-family: $font-family;
    font-weight: 400;
    font-size:   24px;
    line-height: 117%;

    @include laptopXL {
      font-size: 16px;
    }

    @include mobile {
      font-size: 14px;
    }

    strong {
      display:       block;
      font-weight:   600;
      margin-top:    52px;
      margin-bottom: 24px;

      @include mobile {
        margin-top:    40px;
        margin-bottom: 12px;
      }
    }

    &.inline {
      strong {
        display: inline;
      }
    }
  }
}

blockquote {
  position:      relative;
  margin-bottom: 40px;
  @include mobile {
    padding-top: 40px;
  }

  &::before {
    content:  url('data:image/svg+xml,<svg width="32" height="25" viewBox="0 0 32 25" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M13.52 12V24.72H0.8V13.32L6.56 -5.72205e-06H11.96L8.48 12H13.52ZM31.04 12V24.72H18.32V13.32L24.08 -5.72205e-06H29.48L26 12H31.04Z" fill="%23181818"/></svg>');
    display:  block;
    width:    40px;
    height:   40px;
    position: absolute;
    top:      0;
    left:     -54px;

    @include mobile {
      left: 0;
      top:  0;
    }
  }

  //p {
  //  font-size:   24px;
  //  line-height: 117%;
  //}
}

figure.wp-block-image {
  margin-bottom: 40px !important;
}

figcaption {
  margin-top: 24px !important;

  strong {
    font-size:   20px;
    line-height: 130%;
  }
}

.wp-block-table {
  margin-top: 40px;
  max-width:  900px;

  @include mobile {
    min-width:  343px;
    overflow-x: auto;
  }

  table {
    @include mobile {
      width: 900px !important;
    }

    td {
      border: none;
    }

    thead {
      background-color: $gray-80;
      border:           none;

      tr {
        color: $white;

        th {
          padding:     14px 32px;
          border:      none;
          font-weight: 400;
          font-size:   24px;
          line-height: 140%;
          color:       $white;
          text-align:  center;

          &:first-child {
            border-right: 1px solid $white;
          }

          &:last-child {
            border-left: 1px solid $white;
          }
        }
      }
    }

    tbody {
      tr {
        text-align: center;

        &:nth-child(odd) {
          background-color: $gray;
        }

        &:nth-child(even) {
          background-color: $white;
        }
      }

      td {
        padding:     24px 32px;
        font-size:   18px;
        line-height: 140%;
        color:       $gray-80;
        text-align:  center;

        @include mobile {
          padding: 18px 12px;
        }

        &:first-child {
          border-right: 1px solid $black;
        }

        &:last-child {
          border-left: 1px solid $black;
        }
      }
    }
  }
}

.page-template-default {
  &.privacy-policy {
    h1.section-title {
      max-width: 1054px;
    }

    .content__inner {
      max-width:     1054px;
      padding-right: 154px;
    }
  }

  .page-default {
    .container {
      padding-top:    80px;
      padding-bottom: 80px;
      position:       relative;
    }

    .go-home {
      position: absolute;
      top:      80px;
      left:     40px;
    }

    .section-title {
      max-width:    900px;
      margin-left:  auto;
      margin-right: auto;
      font-size:    120px;
    }

  }

  .content {
    background-color: $gray;

    .container {
      padding-top: 54px;
    }

    &__inner {
      max-width:    900px;
      margin-right: auto;
      margin-left:  auto;

      > p {
        margin-bottom: 8px;
      }

      a {
        text-decoration:       underline;
        text-decoration-color: $red;
        text-underline-offset: 4px;

        @include hover {
          color: $red;
        }
      }
    }
  }
}


