@use "../default/mixins";
@use "../variables" as *;

.single-news {
  .news-top {
    .container {
      padding-top:    54px;
      padding-bottom: 80px;
      position:       relative;

      @include mixins.laptopXL {
        padding-top:    40px;
        padding-bottom: 54px;
      }

      @include mixins.mobile {
        padding-top:    20px;
        padding-bottom: 24px;
      }
    }

    .go-home {
      position: absolute;
      top:      54px;
      left:     40px;

      @include mixins.laptopXL {
        top: 38px;
      }
      @include mixins.tablet {
        position:      static;
        margin-bottom: 16px;
      }
    }

    .section-title {
      max-width:    900px;
      margin-left:  auto;
      margin-right: auto;

      @include mixins.laptopXL {
        max-width: 550px;
      }

      @include mixins.mobile {
        max-width: 100%;
      }
    }

    &__date {
      position:       absolute;
      top:            65px;
      left:           15%;
      display:        flex;
      flex-direction: column;
      font-size:      20px;
      line-height:    130%;

      @include mixins.laptopXL {
        top: 48px;
      }

      @include mixins.tablet {
        position:      static;
        margin-bottom: 16px;
        width:         max-content;
      }

      > p {
        @include mixins.mobile {
          font-size: 12px;
        }
      }
    }

    &__num {
      font-family:   $second-family;
      font-weight:   400;
      font-size:     64px;
      display:       block;
      margin-bottom: 24px;
      margin-left:   -6px;

      @include mixins.laptopXL {
        font-size:     54px;
        margin-bottom: 10px;
      }

      @include mixins.mobile {
        font-size:     32px;
        margin-bottom: 8px;
        margin-left:   -2px;
      }
    }
  }

  .news-content {
    background-color: $gray;

    &__inner {
      max-width: 900px;
      margin:    0 auto;

      @include mixins.laptopXL {
        max-width: 550px;
      }
    }
  }

  .news-block {
    &__row {
      display:         flex;
      justify-content: space-between;
      align-items:     center;
      margin-bottom:   80px;

      @include mixins.mobile {
        flex-direction: column;
        align-items:    flex-start;
        row-gap:        10px;
        margin-bottom:  24px;
      }
    }
  }
}